html, body {
  width: 100vw;
  height: 100vh;
  color: #b3b3b3;
  --center-offset: 9em;
  background: #000;
  margin: 0;
  padding: 0;
  font-family: Baskerville, Baskerville Old Face, Hoefler Text, Garamond, Times New Roman, serif;
  font-size: calc(.538462vw + 9.38462px);
  overflow: hidden;
}

* {
  box-sizing: border-box;
}

#container {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

#start-button, .loader, .loader:after {
  --track-color: #8686864d;
  width: 10em;
  height: 10em;
  border: 2px solid var(--track-color);
  border-radius: 50%;
  margin: 0;
  padding: 0;
  font-size: 10px;
}

#start-button, .loader {
  top: calc(50% - 5em - var(--center-offset));
  position: absolute;
  left: calc(50% - 5em);
}

.loader {
  border-left: 2px solid #727272;
  animation: 1.1s linear infinite load8;
  transform: translateZ(0);
}

@keyframes load8 {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

#start-button {
  color: #eee;
  color: #333;
  text-transform: uppercase;
  text-align: center;
  opacity: 0;
  cursor: pointer;
  background: #eee;
  border: none;
  font-weight: bold;
  transition: opacity .4s;
}

#start-button label {
  pointer-events: none;
  letter-spacing: .15em;
  font-size: 1.5em;
}

#intro {
  width: 100%;
  text-align: center;
  top: calc(50% + 8em - var(--center-offset));
  flex-direction: column;
  align-items: center;
  display: flex;
  position: absolute;
}

#intro p {
  max-width: 30em;
  font-size: 1.1em;
  line-height: 1.4em;
}

h1 {
  color: #fff;
  text-transform: uppercase;
  letter-spacing: .2em;
  font-size: 2.3em;
  font-weight: normal;
}

h1 span {
  text-transform: lowercase;
  letter-spacing: 0;
  font-family: Andale Mono, AndaleMono, monospace;
  font-weight: lighter;
}

